          </div>
        </div><!-- /fish-wrapper -->
      </div> <!-- /speacies -->



      <div class="speacies" v-if="alohton">
        <p class="sort"> Altohtone ribe </p>
        <div class="fish-wrapper" v-for="(fish, i) in alohtonFishes" :key="i" :class="{closedSeason : dateCheck(fish.seasonStart, fish.seasonEnd)}">
          <div class="left"> 
            <p class="title"> {{fish.name}}  </p>
            <p class="length"> Dužina: {{fish.minLength || "- "}}cm </p>
          </div>

          <div class="right"> 
            <img :src="'/fish/' + fish.name + '.png'" class="fish-img">
          </div>

