<script>
export default {
  data() {
    return {
      autohton: true,
      alohton: true,
      baned: true,
      url: '../../public/fish/',
      today: (new Date().getMonth()+1) + '/' + new Date().getDate(),
      autohtonFishes: [                                         // format datuma - mesec/dan
        {name: 'Mladica', minLength: 100, seasonStart: '03/01', seasonEnd: '08/31', limit: 1},
        {name: 'Šaran', minLength: 30, seasonStart: '04/01', seasonEnd: '05/31', limit: 3},
        {name: 'Štuka', minLength: 40, seasonStart: '2/01', seasonEnd: '03/31', limit: 3},
        {name: 'Smuđ', minLength: 40, seasonStart: '3/01', seasonEnd: '04/30', limit: 3},
        {name: 'Smuđ kamenjar', minLength: 25, seasonStart: '3/01', seasonEnd: '05/30', limit: 3},
        {name: 'Som', minLength: 60, seasonStart: '5/01', seasonEnd: '06/15', limit: 3},
        {name: 'Bucov', minLength: 30, seasonStart: '04/15', seasonEnd: '06/15', limit: 3},
        {name: 'Manić', minLength: 25, seasonStart: null, seasonEnd: null, limit: 3},
        {name: 'Lipljen', minLength: 30, seasonStart: '03/01', seasonEnd: '05/31', limit: 3},
        {name: 'Pastrmka', minLength: 25, seasonStart: '10/01', seasonEnd: '01/03', limit: 3},
        {name: 'Mrena', minLength: 25, seasonStart: '04/15', seasonEnd: '05/31', limit: 10},
        {name: 'Deverika', minLength: 20, seasonStart: '04/15', seasonEnd: '05/31', limit: 10},
        {name: 'Plotica', minLength: 20, seasonStart: '04/15', seasonEnd: '05/31', limit: 10},
        {name: 'Škobalj', minLength: 20, seasonStart: '04/15', seasonEnd: '05/31', limit: 10},
        {name: 'Školjka', minLength: 20, seasonStart: null, seasonEnd: null, limit: null},
        {name: 'Krkuša', minLength: 20, seasonStart: null, seasonEnd: null, limit: null},
        {name: 'Nosara (šljivar)',  minLength: 20, seasonStart: null, seasonEnd: null, limit: null},
      ],

      alohtonFishes: [                             
        {name: 'Šaran divljak', minLength: 8, seasonStart: '04/01', seasonEnd: '04/10', limit: 3},
      ],

      banedFishes: [                             
        {name: 'Linjak'},
        {name: 'Jegulja'},
        {name: 'Rak'},
        {name: 'Moruna'},
        {name: 'Sig'},
        {name: 'Pastuga'},
        {name: 'Atlantska jesetra'},
        {name: 'Dunavska jesetra'},
        {name: 'Haringa'},
        {name: 'Čikov'},
        {name: 'Vijunica'},
        {name: 'Vretenar'},
        {name: 'Kečiga'},
        {name: 'Zlatni karaš'},
      ],
    }
  },
  methods: {
    dateCheck(from, to) {
      let start = Date.parse(from)
      let end = Date.parse(to)
      let today = Date.parse(this.today)

      if((today <= end && today >= start)) {
        return true;
      }
      return false;
    },
    formated(rawDate) {
      // let stigne = '04/15'
      // let trebami = '15 april'
      const months = ['Januar', 'Februar', 'Mart', 'April', 'Maj', 'Jun', 'Jul', 'Avgust', 'Septembar', 'Oktobar', 'Novembar', 'Decembar']

      let formated = rawDate.split('/') // dolazi '04/15' mesec/dan - a dobijam [04,15]
      let index = parseInt(formated[0])
      return formated[1] + '. ' + months[index]
    }
  }
}
</script>

